<template>
         <van-nav-bar @click="goHome" title="BBIbbi" fixed placeholder :border="false"/>
         <div class="search-box">
            <van-button @click="goSearch">
                <van-icon name="search" />搜索
            </van-button>
        </div> 
    <div class="list">
        <div class="list-item" v-for="item in state.dramaList" :key="item.mId">
            <div class="img">
                <van-image width="90" height="120" 
                :src="item.mImg" @click="goFilmInfo(item.mId)"  />
            </div>
            <div class="title">{{item.mName}}</div>
        </div>    
    </div>

   <TabBar></TabBar>
</template>


<script setup>
import TabBar from "@/components/TabBar/TabBar.vue"
import {useRouter,useRoute} from "vue-router"
import {ref,reactive,onMounted} from "vue"
import { getDramaList } from "@/api/drama"
const router = useRouter()
const route  = useRoute()
const state = reactive({
    dramaList:[]
})

onMounted(async ()=>{
    const { data } = await getDramaList();
    console.log(data)
    data.forEach(item=>{
          item.mScore = parseFloat(item.mScore/2)
          item.mImg = "http://localhost:9999/"+item.mImg
      })
    state.dramaList = data

})

const goFilmInfo = (value)=>{
      router.push({name:"FilmInfo",query:{mId:value}})
  }

  const goSearch = ()=>{
  router.push("/Search")
}

const goHome = ()=>{
  router.push("/")
}


</script>
<style lang="scss" scoped>
:deep(van-nav-bar) {
  background-color: #fff;
  .van-nav-bar__title {
    color: #333 !important;
  }
  .van-nav-bar__left {
    i {
      color: #333 !important;
    }
  }
}

.search-box {
  padding: 2.667vw;
  background-color: #fff;
}

.search-box button {
  width: 100%;
  height: 8vw;
  border-radius: 1.333vw;
  color: #999;
}

.list {
    display: flex;
    flex-wrap: wrap;
    padding: 2.667vw 5.333vw;
    & > :nth-child(3n-2) {
      padding-left: 0 !important;
    }
    .list-item {
      padding-bottom: 2.667vw;
      padding-left: 8.533vw;
      width: 24vw;
      .img {
        :deep(van-image__img) {
          border-radius: 1.333vw;
        }
      }
      .title {
        font-size: 3.733vw;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        color: #191919;
        text-align: center;
      }
    }
  }
  </style>
